html {
  --hover-color: #00acdc;
  font-family: Roboto, sans-serif;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  padding: 23px 0;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: block;
}

.nav_list {
  flex-wrap: nowrap;
  list-style: none;
  display: flex;
}

.nav_item {
  margin-right: 20px;
}

.nav_item:last-child {
  margin-right: 0;
}

.sylka {
  width: 40px;
  height: 40px;
}

.nav__link:hover {
  color: var(--hover-color);
}

.is-active {
  color: var(--hover-color);
  position: relative;
}

.is-active:after {
  content: "";
  background: var(--hover-color);
  border-radius: 10px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
